    /* My added style */

.column1:hover {
    background-color: #FDFAF6;
}
.account-type-text {
    color: #5C5C5C;
    font-size: 16px;
    font-weight: 400;
    padding: 1.5rem 0rem;
}
.account-type-title {
    font-weight: 700;
    font-size: 29px;
}
.img-customer {
    border: 2px solid #FF9C26;
    border-radius: 4px;
}
.img-customer > svg {
    vertical-align: middle;
    margin: 0.15rem;
    padding: 0.15rem;
}
.img-customer > svg > path {
    fill: #FF9C26;
}
.column1:hover > .img-customer {
    background-color: #FF9C26;
}
.column1:hover > .img-customer > svg > path {
    fill: white;
}
.column-container-title {
    font-size: 16px;
}
.cust {
    color: #4F525D;
    font-size: 12px;
    padding-top: 0.35rem;
}
.arrow > svg > path {
    fill: white;
}
.column1:hover > .unknown > .arrow > svg > path {
    fill: #FF9C26;
}
.unknown {
    display: flex;
    justify-content: space-between;
    width: -webkit-fill-available;
}
.arrow{
    margin: auto 0rem;
}
.arrow > svg {
    vertical-align: middle;
}
.sides {
    height: 85vh;
}
.side1 > div > svg {
    margin: 0rem 6rem;
}
.side1 > div {
    margin: 4rem 0rem;
}
.simple-insurance > p {
    padding: 1.1rem 0rem;
    color: rgba(1, 7, 31, 0.6);
}

